      Statement:
        "The SNP opposes LGBTI conversion therapy practices, which are discriminatory and proven to have a harmful effect on the mental health and wellbeing of LGBTI people. There is no place for conversion therapy in Scotland. Such practices are discriminatory and harmful to the mental health and wellbeing of LGBTI people. The UK Government made a commitment to ban conversion therapy in their LGBT Action Plan, and we fully support this, as much of what is needed to take forward an effective ban is reserved to Westminster. If the UK Government does not take forward such a ban, then we will do so within the limits of the Scottish Parliaments powers. I would also note that in 2017, Scotlands CMO on behalf of NHS Scotland, alongside the National Medical Director of NHS England, and a range of medical and therapeutic professional bodies. signed a Memorandum of Understanding on Conversion Therapy, which had the primary purpose of protecting the public through a commitment to ending the practice of conversion therapy in the UK.",
